<template>
<div id="app">
  <div class="container">
    <div class="row">
      <div class="col-12 text-center my-5">
        <img src="./assets/logo.svg" width="300px">
      </div>
    </div>
  </div>
  <div class="container">
    <div class="row justify-content-center text-center text-muted">
      <div class="col-10 mb-5">
        Convert any File to Raw Binary Format for usage in C / C++ Programs
      </div>
    </div>
    <div class="row justify-content-center mb-4">
      <div class="col-10 col-md-8">
        <d-card class="shadow-lg">
          <d-card-body>
            <transition name="fade" mode="out-in">
              <div key="setup" class="container justify-content-center" v-if="!converted">
                <div class="row justify-content-center">
                  <div class="col-12 col-md-10 mb-5 text-muted text-center">
                    <p class="mb-2">Step 1: Select File <small>(10MB max.)</small></p>
                    <br>
                    <div class="custom-file text-left">
                      <input type="file" class="custom-file-input" id="customFile" @change="changeFile" :disabled="loading">
                      <label class="custom-file-label" for="customFile"><span v-if="file == null">Select File</span><span v-else>{{ file.name }}</span></label>
                    </div>
                  </div>
                </div>
                <div class="row justify-content-center">
                  <div class="col-12 mb-5 text-muted text-center">
                    <p class="mb-2">Step 2: Options</p>
                    <br>
                    <d-checkbox inline  v-model="options.gzip" :disabled="loading">Gzip Compress</d-checkbox>
                    <d-checkbox inline  v-model="options.progmem" :disabled="loading">Use PROGMEM (Arduino)</d-checkbox>
                  </div>
                </div>
                <div class="row justify-content-center">
                  <div class="col-8 col-md-4 mb-2 text-muted text-center">
                    <p class="mb-2">Step 3: Convert</p>
                    <br>
                    <d-button pill block-level theme="primary" @click="submitFile" :disabled="loading || file == null"><div v-if="!loading">Covert</div><div v-else class="spinner-border spinner-border-sm"></div></d-button>
                  </div>
                </div>
              </div>
              <div key="response" class="container justify-content-center" v-else>
                <div class="row justify-content-center">
                  <div class="col-12 col-lg-6 mb-4">
                    Converted: &nbsp;
                  </div>
                  <div class="col-12 col-lg-6 mb-4 text-lg-right">
                    <button class="btn btn-secondary btn-sm text-uppercase" v-clipboard="() => converted_response" v-clipboard:success="clipboardTimmer" v-clipboard:error="clipboardError" :disabled="clipboarded"><span v-if="!clipboarded">📋 Copy</span><span v-else>COPIED 🎊</span></button> <!-- &nbsp; <button class="btn btn-sm btn-info text-uppercase">📃 Download</button> -->
                  </div>
                  <div class="col-12">
                    <textarea class="form-control" v-model="converted_response" autocomplete="off" autocorrect="off" autocapitalize="off" spellcheck="false"></textarea>
                  </div>
                  <div class="col-12 mt-4 text-center">
                    <d-button pill theme="success" @click="reset">Convert Another File 🎉</d-button>
                  </div>
                </div>
              </div>
            </transition>
          </d-card-body>
        </d-card>
      </div>
    </div>
    <div class="row justify-content-center my-5">
      <div class="col-6 text-center">
        <p>Made with 💖 by Ayush Sharma (<a href="https://github.com/ayushsharma82" target="_blank">ayushsharma82</a>)</p>
      </div>
      <div class="col-10 mb-4 text-center">
        <a href="https://www.patreon.com/asrocks5" target="_blank"><img class="img-thumbnail" src="./assets/become_a_patron_button.png" width="150px"></a>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'home',

  data(){
    return{
      loading: false,
      options:{
        gzip: false,
        progmem: false
      },
      file: null,
      clipboarded: false,
      converted: false,
      converted_response: "H\n YO\n YEE"
    }
  },

  methods:{
    changeFile(event){
      this.file = event.target.files[0];
    },

    submitFile(){
      if(!this.loading){
        if(this.file == null){
          
          return;
        }

        this.loading = true;
        let formData = new FormData();
        formData.append('gzip', this.options.gzip);
        formData.append('progmem', this.options.progmem);
        formData.append('doc', this.file);

        axios.post('https://ayushsharma82.herokuapp.com/file2raw/file', formData).then((response) => {
          if(response.status == 200){
            this.loading = false;
            this.converted_response = response.data.converted;
            this.converted = true;
          }else{
            this.loading = false;
          }
        }).catch(() => {
          this.loading = false;
        });
      }
    },

    clipboardTimmer(){
      if(!this.clipboarded){
        this.clipboarded = true;
        setTimeout(() => {
          this.clipboarded = false;
        }, 500);
      }
    },

    clipboardError({value}){
      console.log('error', value);
    },

    reset(){
      this.file = null;
      this.converted = false;
      this.converted_response = "";
    }
  }
}
</script>

<style lang="scss">
html{
  overflow-y: scroll;
}

textarea{
  font-family: 'Inconsolata', monospace;
  min-height: 300px;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
